<template>
  <div class="goodsinfo-container">

    <transition
      @beforeEnter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <Swiper :swipeList="swipeList" :isfull="false"></Swiper>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.title }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            <del>{{ goodsinfo.market_price }}</del>&nbsp;&nbsp;
            <span class="now_price">{{ goodsinfo.sell_price }}</span>
          </p>
          <p><NumBox @getcount="getSelectedCount"
          :max="goodsinfo.stock_quantity"></NumBox></p>
          <p>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addToCart"></mt-button>
          </p>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{ goodsinfo.goods_no }}</p>
          <p>{{ goodsinfo.stock_quantity }} </p>
          <p>{{ goodsinfo.add_time | dateFormat }}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../../api/api.js'
import Swiper from '../subcomponents/Swiper.vue'
import NumBox from '../subcomponents/NumBox.vue'

export default {
  data() {
    return {
      id: this.$route.params.id,
      swipeList: [],
      goodsinfo: {}, // 
      ballFlag: false, // 
      selectedCount: 1 // 1
    }
  },
  created() {
    this.getSwipe()
    this.getGoodsInfo()
  },
  methods: {
    getSwipe() {
      this.$ajax.get( api.getthumimages + this.id )
      .then(result => {
        if ( result.data.status === 0 ) {
          //  item  img 
          // item.img  item.src
          result.data.message.forEach(item => {
            item.img = item.src
          })
          this.swipeList = result.data.message
          //console.log(result.data.message);
        }
      })
      .catch(error => {
        Toast('')
      })
    },
    getGoodsInfo(){
      this.$ajax.get( 'http://www.liulongbin.top:3005/api/goods/getinfo/' + this.id )
      .then( result => {
        if( result.data.status === 0 ){
          this.goodsinfo = result.data.message[0]
        }
      })
      .catch(error => {
        Toast('')
      })
    },
    goDesc(id){
      this.$router.push({ name: 'goodsdesc', params: {id} })
    },
    goComment(id){
      this.$router.push({ name: 'goodscomment', params: {id} })
    },
    addToCart(){
      this.ballFlag = !this.ballFlag
      // storecart 
      var goodsinfo = { 
        id: this.id,
        count: this.selectedCount,
        price: this.goodsinfo.sell_price,
        selected: true
      }
      this.$store.commit('addToShoppingCart', goodsinfo)
    },
    beforeEnter(el){
      el.style.transform = "translate(0, 0)"
    },
    enter(el, done){
      el.offsetWidth;
      
      const ballPostion = this.$refs.ball.getBoundingClientRect()
      const badgePostion = document.getElementById('badge').getBoundingClientRect()
      const xDist = badgePostion.left - ballPostion.left
      const yDist = badgePostion.top - ballPostion.top

      el.style.transform = `translate(${xDist}px, ${yDist}px)`
      el.style.transition = "all .5s cubic-bezier(.4, -0.3, 1, .68)"
      done()
    },
    afterEnter(el){
      this.ballFlag = !this.ballFlag
    },
    getSelectedCount(count){
      this.selectedCount = count
    }
  },
  components: {
    Swiper,
    NumBox
  }
}
</script>

<style lang="scss" scoped>
.goodsinfo-container{
  background-color: #eee;
  overflow: hidden;

  .now_price{
    color: red;
    font-size: 16px;
    font-weight: bold;
  }
  .mui-card-footer{
    display: block;
    button{
      margin: 10px 0;
    }
  }
  .ball{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    z-index: 99;
    background-color: red;
    top: 410px;
    left: 147px;
  }
}
</style>
